import { Component, Input, ViewChild } from '@angular/core';
import { RippleModule } from 'primeng/ripple';
import { ButtonModule } from 'primeng/button';
import { FileSelectEvent, FileUpload, FileUploadModule } from 'primeng/fileupload';
import { lastValueFrom, Observable } from 'rxjs';
import {
  selectCurrentUserId,
  selectCurrentUserNickname,
  selectCurrentUserPictureId,
  selectIsLoadingAuth,
} from '@store/auth/auth.selectors';
import { generateImagePath } from '@utils/generate-image-path';
import { Store } from '@ngrx/store';
import { AppStateInterface } from '@store/app/app-state.interface';
import { HttpClient } from '@angular/common/http';
import { MessageService } from 'primeng/api';
import { TranslateModule, TranslateService } from '@ngx-translate/core';
import { readFileAsBase64 } from '@utils/read-file-as-base-64';
import { updateUserImageAction } from '@store/auth/auth.actions';
import { URLS } from '@enums/urls';
import { CommonModule } from '@angular/common';
import { ContainerComponent } from '../container/container.component';
import { ImageComponent } from '@ApiClient/components/image/image.component';
import { ImageCropperComponent } from '../image-cropper/image-cropper.component';
import { RouterLink } from '@angular/router';

export enum PAGE {
  WELCOME_PHOTO = 'welcome-photo',
  SET_PHOTO = 'set-photo',
}

@Component({
  selector: 'app-set-user-image',
  standalone: true,
  imports: [
    RippleModule,
    ButtonModule,
    CommonModule,
    ContainerComponent,
    FileUploadModule,
    ImageComponent,
    ImageCropperComponent,
    TranslateModule,
    RouterLink,
  ],
  templateUrl: 'set-user-image.component.html',
  styleUrls: ['set-user-image.component.scss'],
})
export class SetUserImageComponent {
  @Input() page: PAGE = PAGE.SET_PHOTO;

  @ViewChild('fileUpload') fileUpload!: FileUpload;

  protected readonly URLS = URLS;
  pictureId$: Observable<string | null> = this.store.select(selectCurrentUserPictureId);
  userId$: Observable<string | null> = this.store.select(selectCurrentUserId);
  userNickname$: Observable<string | null> = this.store.select(selectCurrentUserNickname);
  isLoading$: Observable<boolean> = this.store.select(selectIsLoadingAuth);
  imagePaths: string[] = generateImagePath(9, 'assets/images/welcome-photo/photo-profile', 'webp');
  selectedPath: string | null = null;
  fileToCropper: File | null = null;
  croppedFile: string | null = null;
  isRefreshed: boolean = false;
  isDeleted: boolean = false;

  constructor(
    private store: Store<AppStateInterface>,
    private http: HttpClient,
    private toast: MessageService,
    private translate: TranslateService,
  ) {}

  onImageClick(imagePath: string): void {
    this.selectedPath = imagePath;
  }

  async confirm(): Promise<void> {
    try {
      if (this.selectedPath && !this.croppedFile) {
        const res = await lastValueFrom(this.http.get(this.selectedPath, { responseType: 'blob' }));
        const base64String = await readFileAsBase64(res as File);
        this.store.dispatch(updateUserImageAction({ photo: { dataBase64: base64String, fileExt: 'webp' } }));
      } else if (!this.selectedPath && this.croppedFile) {
        this.store.dispatch(updateUserImageAction({ photo: { dataBase64: this.croppedFile, fileExt: 'png' } }));
      }
    } catch (error) {
      console.error('Error during conformation', error);
    }
  }

  refresh() {
    this.onCancelHandler();
    this.isRefreshed = true;
    setTimeout(() => {
      this.isRefreshed = false;
      this.imagePaths = generateImagePath(this.page === PAGE.SET_PHOTO ? 8 : 9, 'assets/images/welcome-photo/photo-profile', 'webp');
    }, 1000);
  }

  onCancelHandler() {
    this.fileUpload.clear();
    this.selectedPath = null;
    this.fileToCropper = null;
    this.croppedFile = null;
    this.isDeleted = false;
  }

  deletePhoto() {
    this.isDeleted = true;
    this.selectedPath = 'assets/images/no-user.webp';
  }

  async onSelectFileUpload(event: FileSelectEvent): Promise<void> {
    this.fileUpload.msgs = [];
    if (event.files.length > 0) {
      const file = event.files[0];
      if (!file.type.startsWith('image/')) {
        this.showErrorMessage('ERROR.WRONG_TYPE');
      } else {
        this.fileToCropper = event.files[0];
        this.selectedPath = null;
      }
    }
  }

  async onSelectCroppedFile(file: string) {
    this.fileUpload.clear();
    this.croppedFile = file;
  }

  private showErrorMessage(key: string): void {
    this.toast.add({
      severity: 'error',
      summary: this.translate.instant('ERROR.TITLE'),
      detail: this.translate.instant(key),
    });
  }

  protected readonly PAGE = PAGE;
}
